import React, { useCallback, useState, useRef, useMemo } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import InputAdornment from '@material-ui/core/InputAdornment';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import Checkbox from '@material-ui/core/Checkbox';
import ClearIcon from '@material-ui/icons/Clear';
import UploadIcon from '@material-ui/icons/Publish';
import CircularProgress from '@material-ui/core/CircularProgress';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import Tooltip from '@material-ui/core/Tooltip';
import useTranslation from '../hooks/useTranslation';

const useStyles = makeStyles((theme) => ({
  realDropBox: {
    cursor: "pointer",
    zIndex: 100,
    width: "100%",
    height: "100%",
    position: "absolute"
  },
  dropBox: {
    cursor: "pointer",
    borderWidth: "2px",
    borderStyle: "dashed",
    borderColor: theme.palette.primary.main,
    margin: theme.spacing(2),
    width: "calc(100% - " + theme.spacing(4) + ")",
    position: "relative"
  },
  dropBoxOver: {
    borderStyle: "solid",
    backgroundColor: theme.palette.primary.light
  },
  fileList: {
    position: "relative",
    margin: theme.spacing(2),
    width: "calc(100% - " + theme.spacing(4) + ")",
    overflow: "scroll"
  },
  fileItem: {
    position: "absolute"
  },
  fileContainer: {
    margin: theme.spacing(2),
    width: "calc(100% - 32px)"
  },
  fileLabel: {
    fontWeight: "bold",
  },
  fileName: {
    whiteSpace: "nowrap",
    overflow: "hidden",
    textOverflow: "ellipsis"
  },
  fileButton: {
    width: "100%",
    justifyContent: "left"
  },
  filter: {
    width: "100%"
  },
  fileDialog: {
    height: "min(600px, 100%)",
    width: "min(500px, 100%)"
  },
  closeButton: {
    position: "absolute",
    right: theme.spacing(1),
    top: theme.spacing(1)
  },
  percentage: {
    marginLeft: theme.spacing(1)
  }
}));

const FileHeaderActions = (props) => {
  const { t } = useTranslation();
  const {
    file, 
    fileList,
    onAddFile,
    onCancel,
    onCancelAll,
    onConfirm,
    onConfirmAll,
    onFileDelete,
    onFileOpen,
    queuedFiles = [],
    needsOverwritePermission
  } = props;
  const inputRef = useRef(null);
  const handleFileSelect = (e) => {
    for(let file of e.target.files) {
      const path = file.name;
      const data = file;
      onAddFile(path, data);
    }
    e.target.value = "";
  };

  const [ filter, setFilter ] = useState("");
  const [ selected, setSelected ] = useState({});
  const [ fileDialogOpen, setFileDialogOpen ] = useState(false);
  const filteredFileList = useMemo(() => fileList.filter(({ file }) => file.toLowerCase().includes(filter.toLowerCase())), [filter, fileList]);

  const handleClearFilter = useCallback((e) => {
    setFilter("");
  }, [ setFilter ]);
  const handleFilterChange = useCallback((e) => {
    setFilter(e.target.value);
  }, [ setFilter ]);
  const handleCloseFileDialog = useCallback((e) => {
    setFileDialogOpen(false);
  }, [ setFileDialogOpen ]);

  const handleOpenFileDialog = useCallback((e) => {
    setFileDialogOpen(true);
  }, [ setFileDialogOpen ]);

  const handleClick = useCallback((e) => {
    inputRef.current.click();
  }, [ inputRef ]);

  const handleSelect = useCallback((e) => {
    if(e.target.checked) {
      setSelected({ ...selected, [e.target.value]: true });
    } else {
      setSelected({ ...selected, [e.target.value]: false });
    }
  }, [ selected, setSelected ]);

  const handleDeleteSelected = useCallback((e) => {
    if(onFileDelete) {
      const deleteFiles = Object.keys(selected).filter((path) => selected[path]);
      deleteFiles.forEach((path) => {
        onFileDelete(path);
      });
      setSelected({});
    }
  }, [ onFileDelete, selected, setSelected ]);

  const handleCancel = useCallback((e) => {
    if(onCancel) {
      onCancel(needsOverwritePermission[0].path);
    }
  }, [ onCancel, needsOverwritePermission ]);

  const handleConfirm = useCallback((e) => {
    if(onConfirm) {
      onConfirm(needsOverwritePermission[0].path);
    }
  }, [ onConfirm, needsOverwritePermission ]);

  const queuedFileList = Object.values(queuedFiles).map((file, index) => ({ ...file, index }));
  queuedFileList.sort((a,b) => { return b.progress-a.progress });
  //const files = queuedFileList.map((file, index) => ({ ...file, index }));
  const fileUploadProgress = queuedFileList.reduce((accum, current) => accum + 1./queuedFileList.length*current.progress, 0)*100;

  const handleFileOpen = useCallback((path) => (e) => {
    if(onFileOpen) {
      onFileOpen(path);
      setFileDialogOpen(false);
    }
  }, [ onFileOpen, setFileDialogOpen ]);
  const classes = useStyles();

  return <>
  <Tooltip placement="top" title={file || t("noFileLoaded")}>
    <Button onClick={handleOpenFileDialog}><span className={classes.fileName}>{ file || t("noFileLoaded") }</span></Button>
  </Tooltip>
  <Dialog open={fileDialogOpen} onClose={handleCloseFileDialog} classes={ { paper: classes.fileDialog }}>
    <DialogTitle>
    <span>{ t("openGCode") }</span>
    <IconButton className={classes.closeButton} onClick={handleCloseFileDialog} size="small"><ClearIcon/></IconButton>
    </DialogTitle>
    <DialogContent>
    <TextField placeholder={t("filter")} value={filter} className={classes.filter} onChange={handleFilterChange} InputProps={{ endAdornment: (filter.length > 0 ? <InputAdornment position="end"><IconButton size="small" onClick={handleClearFilter}><ClearIcon/></IconButton></InputAdornment> : null) }}/>
    <List>
      { filteredFileList.length === 0 ? 
          <ListItem key={"no files"}>{ t("noGCodeFiles") }</ListItem>
        :
        filteredFileList.map(({ file, path }) => {
          return <ListItem key={path}><Checkbox value={path} checked={selected[path] ? true : false} onClick={handleSelect}/><Button classes={{ root: classes.fileButton }} onClick={handleFileOpen(path)}>{ file }</Button></ListItem>
        }) 
      }
    </List>
    </DialogContent>
    <DialogActions>
      <Button color="secondary" onClick={handleDeleteSelected} disabled={Object.keys(selected).filter((path) => selected[path]).length === 0}>{t("deleteSelected")}</Button>
      <Button onClick={handleCloseFileDialog}>{ t("cancel") }</Button>
    </DialogActions>
  </Dialog>
  <Tooltip placement="top" title="Upload Files"><Button onClick={handleClick}><UploadIcon/></Button></Tooltip>
  <CircularProgress value={fileUploadProgress} variant="determinate" size="1em"/>
  { fileUploadProgress > 0 ? <span className={classes.percentage}>{fileUploadProgress.toFixed(2) + "%"}</span> : null }
  <div style={{ display: "none" }}><input onChange={handleFileSelect} type="file" multiple ref={inputRef}/></div>
  <Dialog open={needsOverwritePermission.length > 0}>
    <DialogTitle>Overwrite existing files?</DialogTitle>
    <DialogContent>
      <DialogContentText>
        { needsOverwritePermission.length > 0 ? needsOverwritePermission[0].path : "" } exists on the server{ needsOverwritePermission.length > 1 ? " (and " + needsOverwritePermission.length + " other file" + (needsOverwritePermission.length > 2 ? "s" : "") +")" : "" }. Overwrite?
      </DialogContentText>
      <DialogActions>
        <Button onClick={handleCancel} color="secondary">Cancel</Button>
        { queuedFileList.length > 1 ? <Button onClick={onCancelAll} color="secondary">Cancel All</Button> : null }
        <Button onClick={handleConfirm} color="primary">Overwrite</Button>
        { queuedFileList.length > 1 ?  <Button onClick={onConfirmAll} color="primary">Overwrite All</Button> : null }
      </DialogActions>
    </DialogContent>
  </Dialog>
  </>;
};
export default FileHeaderActions;
