import React from 'react';
import { arcDetails } from './util/math';
import { moveTo, arcTo, lineTo } from './util/path';
import { useTheme } from '@material-ui/core/styles';

//import { makeStyles } from '@material-ui/styles';

//const useStyles = makeStyles((theme) => ({
//  segment: {
//  }
//}));

const ContinuousArcGauge = (props) => {
  const {
    x1,
    y1,
    x2,
    y2,
    x3,
    y3,
    min = 0,
    max = 100,
    value = 100,
    tickThickness = 1,
    paddingStart = 0,
    paddingEnd = 0,
    every
  } = props;

  const theme = useTheme();

  const missingPoints = (typeof(x1) === "undefined" ||
                         typeof(y1) === "undefined" ||
                         typeof(x2) === "undefined" ||
                         typeof(y2) === "undefined" ||
                         typeof(x3) === "undefined" ||
                         typeof(y3) === "undefined");
  const {
    c,
    r, 
    startAngle,
    endAngle
  } = missingPoints ? props : arcDetails(x1,y1,x2,y2,x3,y3);

  const angle = endAngle-startAngle;

  const tickAngle = angle > 0 ? tickThickness/r : -tickThickness/r;
  const paddingStartAngle = angle > 0 ? paddingStart : -paddingStart;
  const paddingEndAngle = angle > 0 ? paddingEnd : -paddingEnd;

  const t = (Math.min(Math.max(value, min), max)-min)/(max-min < .000001 ? 1 : max-min);
  const a = angle-paddingStartAngle-paddingEndAngle;

  const startX = Math.cos(startAngle+paddingStartAngle);
  const startY = Math.sin(startAngle+paddingStartAngle);
  const midX = Math.cos(startAngle+paddingStartAngle+a*t);
  const midY = Math.sin(startAngle+paddingStartAngle+a*t);
  const endX = Math.cos(endAngle-paddingEndAngle);
  const endY = Math.sin(endAngle-paddingEndAngle);
  const r2 = r-tickThickness;

  const largeArc1 = a*t > Math.PI ? 1 : 0;
  const sweepFlag1 = a*t > 0 ? 1 : 0;
  const largeArc2 = a*(1-t) > Math.PI ? 1 : 0;
  const sweepFlag2 = a*(1-t) > 0 ? 1 : 0;
  const arcPathInstructions = [ moveTo(r*startX, r*startY),
                                arcTo(r, r, 0, largeArc1, sweepFlag1, r*midX, r*midY),
                                lineTo(r2*midX, r2*midY),
                                arcTo(r2, r2, 0, largeArc1, 1-sweepFlag1, r2*startX, r2*startY),
                                "Z"
                                ];
  const arcPathInstructions2 = [ moveTo(r*midX, r*midY),
                                 arcTo(r, r, 0, largeArc2, sweepFlag2, r*endX, r*endY),
                                 lineTo(r2*endX, r2*endY),
                                 arcTo(r2, r2, 0, largeArc2, 1-sweepFlag2, r2*midX, r2*midY),
                                 "Z"
                                ];

  const tickElements = [];

  if(every) {
    const ticks = {};
    for(let { step, width } of every) {
      for(let s = Math.ceil(min/step); s <= Math.floor(max/step); s++) {
        const n = s*step;
        const key = n.toFixed(6);
        if(typeof(ticks[key]) === "undefined") {
          ticks[key] = { n, width }
        } else {
          if(width > ticks[key].width) {
            ticks[key].width = width;
          }
        }
      }
    }

    const a = angle-tickAngle-paddingStartAngle-paddingEndAngle;
    for(let key in ticks) {
      const { n, width } = ticks[key];
      const t = (n-min)/(max-min < .000001 ? 1 : max-min);
      const tAngle = t*a+startAngle+paddingStartAngle;

      const largeArc = tickAngle > Math.PI ? 1 : 0;
      const sweepFlag = tickAngle > 0 ? 1 : 0;
      const cosTickAngle = Math.cos(tickAngle);
      const sinTickAngle = Math.sin(tickAngle);
      const tickX1 = r*cosTickAngle
      const tickY1 = r*sinTickAngle
      const smallR = r-width;
      const tickX2 = smallR*cosTickAngle
      const tickY2 = smallR*sinTickAngle

      const pathInstructions = [ 
        moveTo(r, 0),
        arcTo(r, r, 0, largeArc, sweepFlag, tickX1, tickY1),
        lineTo(tickX2, tickY2),
        arcTo(smallR, smallR, 0, largeArc, 1-sweepFlag, smallR, 0),
        "Z" 
      ];
      const fill = n > value ? theme.palette.grey[50] : "black";
      tickElements.push(<path key={key} d={pathInstructions.join(" ")} stroke="none" fill={fill} transform={"rotate(" + (tAngle*180/Math.PI) + ")"}/>);
    }
  }

  return (
  <g transform={"translate(" + c.x + "," + c.y + ")"}>
  <path d={arcPathInstructions.join(" ")} fill="black"/>
  <path d={arcPathInstructions2.join(" ")} fill={theme.palette.grey[50]}/>
  {tickElements}
  </g>
  );
};
export default ContinuousArcGauge;
