import React from 'react';
import Panel from './Panel';
import ThreeBarGauge from './ThreeBarGauge';
import SegmentedArcGauge from './Gauges/SegmentedArcGauge';
import ArcHeader from './Gauges/ArcHeader';
import { useSpring, animated } from 'react-spring';
import Button from '@material-ui/core/Button';
import WarningIcon from '@material-ui/icons/Warning';

import useTranslation from '../hooks/useTranslation';
import useUnits from '../hooks/useUnits';
import { makeStyles } from '@material-ui/styles';

const AnimatedSegmentedArcGauge = animated(SegmentedArcGauge);

const useStyles = makeStyles((theme) => ({
  centerText: {
    ...theme.typography.gaugeText
  },
  pressureHeader: {
    fontSize: 3
  },
  warmUpButton: {
  }
}));

const Spindle = (props) => {
  const {
    rpm,
    load,
    power,
    temperature,
    pressure,
    showWarmUpButton,
    needsWarmUp,
    onWarmUp,
    disableWarmUpButton
  } = props;

  const { t } = useTranslation();
  const { toDisplayUnits } = useUnits();

  const pressureDisplayUnits = toDisplayUnits(pressure);
  const maxPressure = toDisplayUnits({ value: 0.172369, unit: pressure.unit });
  const rpmDisplayUnits = toDisplayUnits(rpm, 'spindleSpeed');
  const powerDisplayUnits = toDisplayUnits(power);
  const temperatureDisplayUnits = toDisplayUnits(temperature);
  const temperatureArcMin = toDisplayUnits({value: 32, unit: "F"}).value;
  const temperatureArcMax = toDisplayUnits({value: 175, unit: "F"}).value;
  const classes = useStyles();

  const pressureSpring = useSpring({ from: { value: 0 }, value: pressureDisplayUnits.value });

  const hidePressure = pressure.value <= -999;

  const maxPower = 2500; // max power for solo, should be retrieved from Rockhopper for compatibility with other machines

  return <Panel label="Spindle">
    <ThreeBarGauge value0={powerDisplayUnits.value}
                   min0={0}
                   max0={maxPower}
                   units0={powerDisplayUnits.unit}
                   decimalPoints0={0}
                   label0={t("power")}

                   value1={load}
                   min1={0}
                   max1={100}
                   units1={"%"}
                   decimalPoints1={1}
                   label1={t("load")}

                   value2={temperatureDisplayUnits.value}
                   min2={temperatureArcMin}
                   max2={temperatureArcMax}
                   units2={"" + temperatureDisplayUnits.unit}
                   decimalPoints2={1}
                   label2={t("temperature")}
                   hideValue2={temperature.value <= -998}

                   centerValue={rpmDisplayUnits.value}
                   centerLabel={rpmDisplayUnits.unit}
                   >
    { hidePressure ? null :
    <>
      <animated.text x={4} 
            y={9} 
            textAnchor="left" 
            alignmentBaseline="middle" 
            className={classes.centerText} 
            style={ { fontSize: 3 }}>
        {pressureSpring.value.interpolate((x) => (Math.round(x*100)/100).toLocaleString("en-US")+ " " + pressureDisplayUnits.unit)}
      </animated.text>
      <AnimatedSegmentedArcGauge c={ { x: 8, y: 8 } } 
                         r={8} 
                         startAngle={-270*Math.PI/180}
                         endAngle={-30*Math.PI/180}
                         segments={pressureSpring.value.interpolate((x) => 10*x/maxPressure.value)}
                         segmentRatio={1} 
                         segmentWidth={2}
                         totalSegments={10}
                         paddingStart={0}
                         paddingEnd={0}/>
      <ArcHeader c={ { x: 8, y: 8 } } 
                 r={10} 
                 startAngle={-150*Math.PI/180}
                 endAngle={-30*Math.PI/180}
                 text={t("pressure")} 
                 paddingStart={0} 
                 paddingEnd={0}
                 className={classes.pressureHeader}/> 
     
                         </> }

    </ThreeBarGauge>
    { showWarmUpButton ?
    <Button disabled={disableWarmUpButton} variant="contained" onClick={onWarmUp} color={needsWarmUp ? "primary" : "default"} classes={{ root: classes.warmUpButton }}>{ needsWarmUp ? <WarningIcon/> : null } {t("warmUp")}</Button>
    : null }
  </Panel>;
};
export default Spindle;
