import { connect } from 'react-redux';
import FileHeaderActions from '../components/FileHeaderActions';
import actions from '../modules/rockhopper/actions';
import fileQueue from '../modules/rockhopper/file-queue'

import { selectProgramList, selectProgram, selectFileUploadsThatNeedOverwritePermission, selectFileUploads } from '../modules/rockhopper/selectors';
import { FILE_TYPES } from '../modules/rockhopper/constants';

export default connect( 
(state, ownProps) => ({
  file: selectProgram(state),
  needsOverwritePermission: selectFileUploadsThatNeedOverwritePermission(state),
  queuedFiles: selectFileUploads(state),
  fileList: selectProgramList(state)
}), 
(dispatch, ownProps) => ({ 
  onCancelAll: () => dispatch(actions.fileUpload.cancelAll(FILE_TYPES.PROGRAM)),
  onCancel: (path) => {console.log('canceling in container FileHeaderActions'); dispatch(actions.fileUpload.cancel({path: path, fileType: FILE_TYPES.PROGRAM}))},
  onConfirm: (path) => {console.log('confirming in container FileHeaderActions'); dispatch(actions.fileUpload.confirmOverwrite({path: path, fileType: FILE_TYPES.PROGRAM}))},
  onConfirmAll: () => dispatch(actions.fileUpload.confirmOverwriteAll(FILE_TYPES.PROGRAM)),
  onAddFile: (path, data) => fileQueue.addFile(path, FILE_TYPES.PROGRAM, data),
  onFileOpen: (path) => dispatch(actions.put.programOpenCmd({ data: [ path, FILE_TYPES.PROGRAM ] })),
  // TODO - Add CommandItem that can delete multiple files at once
  onFileDelete: (path) => dispatch(actions.put.programDelete({ data: [ path, FILE_TYPES.PROGRAM ] }))
}))(FileHeaderActions); 
