import React from 'react';

import Gauges from './Gauges';
import SegmentedArcGauge from './Gauges/SegmentedArcGauge';
import ContinuousArcGauge from './Gauges/ContinuousArcGauge';
import ArcIndicator from './Gauges/ArcIndicator';
import ArcText from './Gauges/ArcText';
import ArcHeader from './Gauges/ArcHeader';

import { useSpring, animated } from 'react-spring';
import { makeStyles, useTheme } from '@material-ui/styles';

const AnimatedSegmentedArcGauge = animated(SegmentedArcGauge);
const AnimatedContinuousArcGauge = animated(ContinuousArcGauge);
const AnimatedArcIndicator = animated(ArcIndicator);
const AnimatedArcText = animated(ArcText);

const useStyles = makeStyles((theme) => ({
  centerText: {
    ...theme.typography.rpmText
  },
  gaugeHeader: {
    ...theme.typography.gaugeHeader
  }
}));

const ThreeBarGauge = (props) => {
  const {
    value0 = 0,
    min0 = 0,
    max0 = 1,
    units0 = "",
    decimalPoints0 = 0,
    label0 = "Label 0",
    hideValue0 = false,

    value1 = 0,
    min1 = 0,
    max1 = 1,
    units1 = "",
    decimalPoints1 = 0,
    label1 = "Label 1",
    hideValue1 = false,

    value2 = 0,
    min2 = 0,
    max2 = 1,
    units2 = "",
    decimalPoints2 = 0,
    label2 = "Label 2",
    hideValue2 = false,

    centerValue,
    centerLabel,
    children
  } = props;

  const spring0 = useSpring({ from: { value: min0 }, value: value0 });
  const spring1 = useSpring({ from: { value: min1 }, value: value1 });
  const spring2 = useSpring({ from: { value: min2 }, value: value2 });
  const centerSpring = useSpring({ from: { value: 0 }, value: centerValue });

  const max_min0 = (max0-min0 < .000001 ? 1 : max0-min0);
  const max_min1 = (max1-min1 < .000001 ? 1 : max1-min1);
  const max_min2 = (max2-min2 < .000001 ? 1 : max2-min2);

  const padding = 3*Math.PI/180;
  const classes = useStyles();
  const theme = useTheme();

  return <Gauges width={"100%"}>
      { /* Gauge 0 */ }
      <AnimatedSegmentedArcGauge c={ { x: 50, y: 50 } } 
                                 r={48} 
                                 startAngle={150*Math.PI/180} 
                                 endAngle={270*Math.PI/180} 
                                 segments={spring0.value.interpolate((x) => (x-min0)/(max_min0)*5)} 
                                 segmentRatio={.95} 
                                 segmentWidth={5} 
                                 totalSegments={5} 
                                 paddingStart={padding} 
                                 paddingEnd={padding}/> 
      <AnimatedContinuousArcGauge c={ { x: 50, y: 50 } } 
                                  r={42} 
                                  startAngle={150*Math.PI/180} 
                                  endAngle={270*Math.PI/180} 
                                  tickThickness={.2} 
                                  tickWidth={5} 
                                  value={spring0.value} 
                                  min={min0} 
                                  max={max0} 
                                  paddingStart={padding} 
                                  paddingEnd={padding} 
                                  every={ [ { step: 10, width: 1.5 }, { step: 5, width: 1 } ] }/> 
      <AnimatedArcIndicator c={ {x: 50, y: 50}} 
                            r={42} 
                            startAngle={150*Math.PI/180} 
                            endAngle={270*Math.PI/180} 
                            min={min0} 
                            max={max0} 
                            value={spring0.value} 
                            thickness={-1} 
                            width={2} 
                            paddingStart={padding} 
                            paddingEnd={padding}/>
      { hideValue0 ? null : 
      <AnimatedArcText c={ {x: 50, y: 50}} 
                       r={50} 
                       startAngle={150*Math.PI/180} 
                       endAngle={270*Math.PI/180} 
                       min={min0} 
                       max={max0} 
                       value={spring0.value} 
                       paddingStart={padding} 
                       paddingEnd={padding} 
                       fontSize={3} 
                       textAnchor={"end"} 
                       units={units0}
                       decimalPoints={decimalPoints0}/> }
      <ArcHeader c={ { x: 50, y: 50 } } 
                 r={30} 
                 startAngle={150*Math.PI/180} 
                 endAngle={270*Math.PI/180} 
                 text={label0} 
                 paddingStart={padding} 
                 paddingEnd={padding}/> 

      { /* Gauge 1 */ }
      <AnimatedSegmentedArcGauge c={ { x: 50, y: 50 } } 
                                 r={48} 
                                 startAngle={30*Math.PI/180} 
                                 endAngle={-90*Math.PI/180} 
                                 segments={spring1.value.interpolate((x) => (x-min1)/(max_min1)*50)} 
                                 segmentRatio={.95} 
                                 segmentWidth={5} 
                                 totalSegments={50} 
                                 paddingStart={padding} 
                                 paddingEnd={padding}/>
      <AnimatedContinuousArcGauge c={ { x: 50, y: 50 } } 
                                  r={42} 
                                  startAngle={30*Math.PI/180} 
                                  endAngle={-90*Math.PI/180} 
                                  tickThickness={.2} 
                                  tickWidth={5} 
                                  value={spring1.value} 
                                  min={min1} 
                                  max={max1} 
                                  paddingStart={padding} 
                                  paddingEnd={padding} 
                                  every={ [ { step: 10, width: 1 } ] }/>
      <AnimatedArcIndicator c={ {x: 50, y: 50}} 
                            r={42} 
                            startAngle={30*Math.PI/180} 
                            endAngle={-90*Math.PI/180} 
                            min={min1} 
                            max={max1} 
                            value={spring1.value} 
                            thickness={-1} 
                            width={2} 
                            paddingStart={padding} 
                            paddingEnd={padding}/>
      { hideValue1 ? null : 
      <AnimatedArcText c={ {x: 50, y: 50}} 
                       r={50} 
                       startAngle={30*Math.PI/180} 
                       endAngle={-90*Math.PI/180} 
                       min={min1} 
                       max={max1} 
                       value={spring1.value} 
                       paddingStart={padding} 
                       paddingEnd={padding} 
                       fontSize={3} 
                       textAnchor={"start"} 
                       units={units1} 
                       decimalPoints={decimalPoints1}/> }
      <ArcHeader c={ { x: 50, y: 50 } } 
                 r={30} 
                 startAngle={-90*Math.PI/180} 
                 endAngle={30*Math.PI/180} 
                 text={label1} 
                 paddingStart={padding} 
                 paddingEnd={padding}/> 

      { /* Gauge 2 */ }
      <AnimatedSegmentedArcGauge c={ { x: 50, y: 50 } } 
                                 r={48} 
                                 startAngle={30*Math.PI/180} 
                                 endAngle={150*Math.PI/180} 
                                 segments={spring2.value.interpolate((x) => (x-min2)/(max_min2)*50)} 
                                 segmentRatio={.6} 
                                 segmentWidth={5} 
                                 totalSegments={50} 
                                 paddingStart={padding} 
                                 paddingEnd={padding}/> 
      <AnimatedArcIndicator c={ {x: 50, y: 50}} 
                            r={42} 
                            startAngle={30*Math.PI/180} 
                            endAngle={150*Math.PI/180} 
                            min={min2} 
                            max={max2} 
                            value={spring2.value} 
                            thickness={-1} 
                            width={2} 
                            paddingStart={padding} 
                            paddingEnd={padding}/>
      { hideValue2 ? null :                        
      <AnimatedArcText c={ {x: 50, y: 50}} 
                       r={55} 
                       startAngle={30*Math.PI/180} 
                       endAngle={150*Math.PI/180} 
                       min={min2} 
                       max={max2} 
                       value={spring2.value} 
                       paddingStart={padding} 
                       paddingEnd={padding} 
                       fontSize={3} 
                       textAnchor={"middle"} 
                       units={units2} 
                       decimalPoints={decimalPoints2}/> }
      <ArcHeader c={ { x: 50, y: 50 } } 
                 r={30} 
                 startAngle={150*Math.PI/180} 
                 endAngle={30*Math.PI/180} 
                 text={label2} 
                 dy={3} 
                 paddingStart={padding} 
                 paddingEnd={padding}/> 

      <animated.text x={50} 
                     y={50} 
                     textAnchor="middle" 
                     alignmentBaseline="middle" 
                     className={classes.centerText} 
                     fill="white" 
                     stroke={theme.palette.primary.main} 
                     strokeWidth={.2} 
                     style={ { letterSpacing: 0 } }>
       {centerSpring.value.interpolate((x) => Math.round(x).toLocaleString("en-US"))}
      </animated.text>
      <text x={60} 
            y={60} 
            textAnchor="middle" 
            alignmentBaseline="middle" 
            className={classes.gaugeHeader} 
            style={ { fontSize: 5 }}>
        {centerLabel}
      </text>
      {children}

    </Gauges>;
};
export default ThreeBarGauge;
