import React from 'react';
// import clsx from "clsx";
import { lighten, makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
// import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
// import TablePagination from '@material-ui/core/TablePagination';
import TableRow from '@material-ui/core/TableRow';
import TableSortLabel from '@material-ui/core/TableSortLabel';
// import Toolbar from '@material-ui/core/Toolbar';
// import Typography from '@material-ui/core/Typography';
// import Paper from '@material-ui/core/Paper';
import Checkbox from '@material-ui/core/Checkbox';
// import IconButton from '@material-ui/core/IconButton';
import Tooltip from '@material-ui/core/Tooltip';
// import FormControlLabel from '@material-ui/core/FormControlLabel';
// import Switch from '@material-ui/core/Switch';
// import DeleteIcon from '@material-ui/icons/Delete';
// import FilterListIcon from '@material-ui/icons/FilterList';
import HelpIcon from '@material-ui/icons/Help';

import FavoritesTableBody from './FavoritesTableBody';
import FavoritesTableToolbar from './FavoritesTableToolbar';
import SubmittableDialog from './SubmittableDialog';
import { SORT_ORDER } from '../modules/pentaui/constants';


const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
  },
  paper: {
    width: '100%',
    marginBottom: theme.spacing(2),
  },
  table: {
    // minWidth: 750,
  },
  visuallyHidden: {
    border: 0,
    clip: 'rect(0 0 0 0)',
    height: 1,
    margin: -1,
    overflow: 'hidden',
    padding: 0,
    position: 'absolute',
    top: 20,
    width: 1,
  },
  rootToolbar: {
    paddingLeft: theme.spacing(2),
    paddingRight: theme.spacing(1)
  },
  highlight:
    theme.palette.type === "light"
      ? {
          color: theme.palette.secondary.main,
          backgroundColor: lighten(theme.palette.secondary.light, 0.85)
        }
      : {
          color: theme.palette.text.primary,
          backgroundColor: theme.palette.secondary.dark
        },
  title: {
    flex: "1 1 100%"
  }
}));

const headCells = [
  { id: 'cmd', numeric: true, disablePadding: false, label: 'Command' },
  { id: 'name', numeric: true, disablePadding: false, label: 'Name' },
  { id: 'time', numeric: true, disablePadding: false, label: 'Date Added' },
];

const FavoritesTable = (props) => {
  const {
    favorites,
    onNameFavorite,
    onDelete,
    onClickItem,
  } = props;  
  
  const classes = useStyles();
  const [selected, setSelected] = React.useState(new Set());
  const [sortOrder, setSortOrder] = React.useState(SORT_ORDER.ASCENDING);
  const [sortKey, setSortKey] = React.useState(headCells[0].id);
  const [dialogOpen, setDialogOpen] = React.useState(false);
  const [filter, setFilter] =  React.useState("")
  // const [filterKey, setFilterKey] =  React.useState(headCells[0].id)
  const filterKey = headCells[0].id;

  const handleSortRequest = (key) => {
    const isAscending = key === sortKey && sortOrder === SORT_ORDER.ASCENDING;
    setSortOrder(isAscending ? 'desc' : 'asc');
    setSortKey(key);
  }

  const handleClickItemCheckbox = (event, key) => {
    let newSet = new Set(selected);
    if(selected.has(key)){
      newSet.delete(key);
      setSelected(newSet);
    }
    else{
      newSet.add(key);
      setSelected(newSet);
    }
  };

  const handleSelectAll = () => {
    if(selected.size < favorites.length){
      let newSet = new Set(selected);
      favorites.forEach((favorite) => newSet.add(favorite.cmd))
      setSelected(newSet);
    }
    else{
      setSelected(new Set());
    }
  }

  const handleNameItem = (e, item) => {
    onNameFavorite(item.cmd, e.target.value)
  }

  const handleDelete = () => {
    onDelete(selected);
  }

  const handleCloseDialog = () => {
    setDialogOpen(false);
  };

  const handleSubmitDialog = (value) => {
    setFilter(value)
    setDialogOpen(false);
  };


  return (
    <div>
    <TableContainer>
      <Table
        className={classes.table}
        aria-labelledby="tableTitle"
        aria-label="favorites table"
        stickyHeader
      >
      <TableHead>
        <TableRow>
          <TableCell padding="checkbox">
            <Checkbox
              onClick={handleSelectAll}
              // indeterminate={numSelected > 0 && numSelected < rowCount}
              // checked={rowCount > 0 && numSelected === rowCount}
              // onChange={onSelectAllClick}
              inputProps={{ 'aria-label': 'select all desserts' }}
            />
          </TableCell>
          {headCells.map((headCell) => 
          { 
            return headCell.id === "name" ? (
              <TableCell
                key={headCell.id}
                align={'left'}
                // align={headCell.numeric ? 'right' : 'left'}
                padding={headCell.disablePadding ? 'none' : 'default'}
                sortDirection={sortKey === headCell.id ? sortOrder : false}
              >
                <TableSortLabel
                  active={sortKey === headCell.id}
                  direction={sortKey === headCell.id ? sortOrder : 'asc'}
                  onClick={(e) => handleSortRequest(headCell.id)}
                >
                  {headCell.label}
                  {sortKey === headCell.id ? (
                    <span className={classes.visuallyHidden}>
                      {sortOrder === SORT_ORDER.DESCENDING ? 'sorted descending' : 'sorted ascending'}
                    </span>
                  ) : null}
                </TableSortLabel>
                <Tooltip title={<span>{"Named MDI commands will appear in the Quick Access panel."}</span>}><HelpIcon/></Tooltip>
              </TableCell>
            ) : (
              <TableCell
                key={headCell.id}
                align={'left'}
                // align={headCell.numeric ? 'right' : 'left'}
                padding={headCell.disablePadding ? 'none' : 'default'}
                sortDirection={sortKey === headCell.id ? sortOrder : false}
              >

                <TableSortLabel
                  active={sortKey === headCell.id}
                  direction={sortKey === headCell.id ? sortOrder : 'asc'}
                  onClick={(e) => handleSortRequest(headCell.id)}
                >
                  {headCell.label}
                  {sortKey === headCell.id ? (
                    <span className={classes.visuallyHidden}>
                      {sortOrder === SORT_ORDER.DESCENDING ? 'sorted descending' : 'sorted ascending'}
                    </span>
                  ) : null}
                </TableSortLabel>
              </TableCell>
            )
          }
          )}
        </TableRow>
      </TableHead>

      <FavoritesTableBody
        favorites={favorites}
        sortOrder={sortOrder}
        sortKey={sortKey}
        filter={filter}
        filterKey={filterKey}
        onSelectItem={handleClickItemCheckbox}
        onNameItem={handleNameItem}
        onClickItem={onClickItem}
        selected={selected}
      />
    </Table>
  </TableContainer>
  <FavoritesTableToolbar
    onDelete={() => handleDelete(selected)}
    onOpenFilterModal={() => setDialogOpen(true)}
    filter={filter}
    selected={selected}
    numSelected={selected.length}
  />
  <SubmittableDialog
    open={dialogOpen}
    value={""}
    onSubmit={handleSubmitDialog}
    onClose={handleCloseDialog}
    title={"Filter Favorites"}
    textFieldLabel={"Command"}
    submitBtnLabel={"Filter"}
    cancelBtnLabel="Cancel"
  />
    </div>
  )
};

export default React.memo(FavoritesTable)