import React from 'react';
import { arcDetails } from './util/math';
import { moveTo, arcTo, lineTo } from './util/path';
//import { useTheme } from '@material-ui/core/styles';

const ArcIndicator = (props) => {
  const {
    x1,
    y1,
    x2,
    y2,
    x3,
    y3,
    min = 0,
    max = 100,
    value = 100,
    paddingStart = 0,
    paddingEnd = 0,
    thickness,
    width
  } = props;

  if(isNaN(value)) {
    console.error("value must be a number");
  }

//  const theme = useTheme();

  const missingPoints = (typeof(x1) === "undefined" ||
                         typeof(y1) === "undefined" ||
                         typeof(x2) === "undefined" ||
                         typeof(y2) === "undefined" ||
                         typeof(x3) === "undefined" ||
                         typeof(y3) === "undefined");
  const {
    c,
    r, 
    startAngle,
    endAngle
  } = missingPoints ? props : arcDetails(x1,y1,x2,y2,x3,y3);

  const angle = endAngle-startAngle;

  const paddingStartAngle = angle > 0 ? paddingStart : -paddingStart;
  const paddingEndAngle = angle > 0 ? paddingEnd : -paddingEnd;

  const t = (Math.min(Math.max(value, min), max)-min)/(max-min < .000001 ? 1 : max-min);
  const a = angle-paddingStartAngle-paddingEndAngle;

  const baseR = r-thickness;
  const baseAngle = angle > 0 ? width/baseR : -width/baseR;

  const startX = Math.cos(startAngle+paddingStartAngle+a*t-.5*baseAngle);
  const startY = Math.sin(startAngle+paddingStartAngle+a*t-.5*baseAngle);
  const midX = Math.cos(startAngle+paddingStartAngle+a*t);
  const midY = Math.sin(startAngle+paddingStartAngle+a*t);
  const endX = Math.cos(startAngle+paddingStartAngle+a*t+.5*baseAngle);
  const endY = Math.sin(startAngle+paddingStartAngle+a*t+.5*baseAngle);

  const largeArc = 0;
  const sweepFlag = a*t > 0 ? 1 : 0;

  const indicatorPathInstructions = [ moveTo(r*midX, r*midY),
                                lineTo(baseR*startX, baseR*startY),
                                arcTo(baseR, baseR, 0, largeArc, sweepFlag, baseR*endX, baseR*endY),
                                "Z"
                                ];

  return (
  <g transform={"translate(" + c.x + "," + c.y + ")"}>
  <path d={indicatorPathInstructions.join(" ")} fill="black"/>
  </g>
  );
};
export default ArcIndicator;
