import React, { useCallback } from 'react';
import Grid from '@material-ui/core/Grid';
import MdiQuickAccessButton from './MdiQuickAccessButton';

const MdiQuickAccessGrid = (props) => {
  const {
    mdiFavorites,
    onClickItem,
  } = props;

  const handleClickItem = useCallback((cmd) => {
    if(onClickItem){
      onClickItem(cmd);
    }
  }, [onClickItem]);

  return(
    <Grid container spacing={4}>
      { !mdiFavorites ? null : (
          mdiFavorites.map(
            ({cmd, name}, idx) => {
              if(name && name.length > 0){
              
              return (
                <Grid item key={idx} xs={4}>
                  <MdiQuickAccessButton
                    name={name}
                    cmd={cmd}
                    onClick={handleClickItem}
                    size="large"
                    fullWidth={true}
                    variant="contained"
                    color="secondary"
                  />
                </Grid>
              )
              } else return null
            }
          )
        )
      }
    </Grid>
  )
};

export default React.memo(MdiQuickAccessGrid)
