import { connect } from 'react-redux';
import KeyboardShortcuts from '../components/KeyboardShortcuts';
import { tick } from '../util/sounds';
import actions from '../modules/rockhopper/actions';
import pentaActions from '../modules/pentaui/actions';
import { MANUAL_TAB, SETUP_TAB } from '../components/Tabs';
import { selectLocation } from '../modules/router/selectors';
import { selectEnableKeyboardJogging, selectAxisSelectJogDigitIndex, selectAxisSelectJogAxis } from '../modules/pentaui/selectors';
import { selectAxes,
         selectMaxLinearVelocity, 
         selectMachineStatus, 
         selectDelayedRapidRatePercentage, 
         selectDelayedMaxVelPercentage, 
         selectDelayedFeedratePercentage, 
         selectDelayedSpindle, 
         selectTrajCoordinates,
         selectKeyboardShortcutsMap,
         selectPositions,  
         selectUnitsCode,
         selectDisplayUnits } from '../modules/rockhopper/selectors';
import { toMachineUnits } from '../hooks/useUnits';

export const stringToActionMap = {
  'estopDisable': () => actions.put.estopCmd({data:[false]}),
  'estopEnable': () => actions.put.estopCmd({data:[true]}),
  'feedrateDecrement1': () => actions.put.incrementFeedrateCmd({data: [-0.01]}),
  'feedrateIncrement1': () => actions.put.incrementFeedrateCmd({data: [+0.01]}),
  'feedrateDecrement10': () => actions.put.incrementFeedrateCmd({data: [-0.1]}),
  'feedrateIncrement10': () => actions.put.incrementFeedrateCmd({data: [+0.1]}),
  'spindlerateDecrement1': () => actions.put.incrementSpindleOverrideCmd({data: [-0.01]}),
  'spindlerateIncrement1': () => actions.put.incrementSpindleOverrideCmd({data: [+0.01]}),
  'spindlerateDecrement10': () => actions.put.incrementSpindleOverrideCmd({data: [-0.1]}),
  'spindlerateIncrement10': () => actions.put.incrementSpindleOverrideCmd({data: [+0.1]}),
  'homeAll': () => actions.put.homeCmd(),
  'feedHold': () => actions.put.feedHoldCmd(),
  'step': () => actions.put.autoCmd({ data: [ "AUTO_STEP" ] }),
  'cycleStart': () => actions.put.cycleStartCmd(),
  'reset': () => actions.put.abortCmd(),
}

export const stringToAxisSelectJogMap = {
  'axisSelectJogSelectX': () => pentaActions.axisSelectJog.setAxis('X'),
  'axisSelectJogSelectY': () => pentaActions.axisSelectJog.setAxis('Y'),
  'axisSelectJogSelectZ': () => pentaActions.axisSelectJog.setAxis('Z'),
  'axisSelectJogSelectA': () => pentaActions.axisSelectJog.setAxis('A'),
  'axisSelectJogSelectB': () => pentaActions.axisSelectJog.setAxis('B'),
  'axisSelectJogSelectC': () => pentaActions.axisSelectJog.setAxis('C'),
  'axisSelectJogIncreaseStepSize': () => pentaActions.axisSelectJog.incrementDigitIndex(1),
  'axisSelectJogDecreaseStepSize': () => pentaActions.axisSelectJog.incrementDigitIndex(-1),
  'axisSelectJogNextAxis': (axisSelectJogAxis, trajCoords) => pentaActions.axisSelectJog.incrementAxis({inc: 1, axisSelectJogAxis, trajCoords}),
  'axisSelectJogPreviousAxis': (axisSelectJogAxis, trajCoords) => pentaActions.axisSelectJog.incrementAxis({inc: -1, axisSelectJogAxis, trajCoords}),
}

export default connect( 
(state, ownProps) => {
  return { 
    enable: true,
    axes: selectAxes(state),
    location: selectLocation(state).pathname,
    enableKeyboardJogging: selectEnableKeyboardJogging(state),
    maxVel: selectDelayedMaxVelPercentage(state),
    feedrate: selectDelayedFeedratePercentage(state),
    spindle: selectDelayedSpindle(state),
    maxRapid: selectDelayedRapidRatePercentage(state),
    machineStatus: selectMachineStatus(state),
    maxLinearVelocity: selectMaxLinearVelocity(state),
    positions: selectPositions(state),
    shortcutsMap: selectKeyboardShortcutsMap(state),
    trajCoords: selectTrajCoordinates(state),
    axisSelectJogAxis: selectAxisSelectJogAxis(state),
    axisSelectJogDigitIndex: selectAxisSelectJogDigitIndex(state),
    displayUnits: selectDisplayUnits(state),
    unitsCode: selectUnitsCode(state)
  }
}, 
(dispatch, ownProps) => ({ 
// These will fire when keyboard shortcuts are available or not. 
// We should probably dispatch actions to make this information
// available in the UI with some kind of indicator (in the old
// UI there was a blue or orange button in the bottom bar).
  onFocus: () => {
//    console.log("keyboard shortcuts focused, can receive key events")
  },
  onBlur: () => {
//    console.log("keyboard shortcuts blurred, cannot receive key events")
  },
  onJogContinuous: (axis, speed) => {
    dispatch(actions.putHelpers.jogContinuous({ axis, speed }));
  },
  onJogIncrement: (axis, speed, increment) => {
    dispatch(actions.putHelpers.jogIncrement({ axis, speed, increment }));
  },
  onJogStop: (axis) => {
    dispatch(actions.putHelpers.jogStop({ axis }));
  },
  onAction: dispatch,
}), (stateProps, dispatchProps, ownProps) => {
  const {
    enable,
    location,
    enableKeyboardJogging,
    shortcutsMap,
    trajCoords,
    axisSelectJogAxis,
    axisSelectJogDigitIndex,
    displayUnits,
    unitsCode
  } = stateProps;
  const {
    onFocus,
    onBlur,

    onJogContinuous,
    onJogIncrement,
    onJogStop,
    
    onAction,
  } = dispatchProps;
  return {
    enable,
    onFocus,
    onBlur,
    onKeyDown: (e) => {
      const {
        nativeEvent,
        shiftKey,
        altKey,
        ctrlKey,
        metaKey,
        repeat,
      } = e;
      const modifierString = (ctrlKey ? 'Ctrl+' : '') + (shiftKey ? 'Shift+' : '') + (altKey ? 'Alt+' : '') + (metaKey ? 'Meta+' : '');
      let code = nativeEvent.code;
      if(code.startsWith('Digit')){
        code = code.slice(5);
      }
      else if(code.startsWith('Key')){
        code = code.slice(3);
      }
      const keyCombination = modifierString + code;
      if(!repeat && shortcutsMap.hasOwnProperty(keyCombination)){
        const cmd = shortcutsMap[keyCombination];
        if(cmd.startsWith('jog')){ //jog commands are strings with form: jog{axis}{direction}{optional modifier, either slow or incremental}.
          if(location.startsWith(MANUAL_TAB) || location.startsWith(SETUP_TAB)) {
            if(enableKeyboardJogging) {
              const jointNum = trajCoords.indexOf(cmd[3]); //The 4th character is an uppercase letter representing an axis: X, Y, Z, A, B, or C
              if(jointNum !== -1){
                const direction = cmd[4] === '+' ? +1 : -1; //The 5th character represents direction: + or -
                const speed = jointNum > 2 ? 10 : 1;
                if(cmd.includes('slow')){
                  onJogContinuous(jointNum, speed * direction * 0.1);
                }
                else if(cmd.includes('incremental')){
                  const displayUnit = jointNum > 2 ? displayUnits.angle : displayUnits.length;
                  const incrementBase = jointNum > 2 ? 0.001 : displayUnit === 'mm' ? 0.001 : 0.0001;
                  const magnitude = axisSelectJogDigitIndex ? axisSelectJogDigitIndex : 1;
                  const increment = incrementBase * Math.pow(10, magnitude - 1);
                  const machineUnitIncrement = toMachineUnits({value: increment, unit: displayUnit}, unitsCode).value;
                  onJogIncrement(jointNum, direction * speed, machineUnitIncrement);
                }
                else{
                  onJogContinuous(jointNum, speed * direction);
                }
                tick.play();
              }
            }
          }
        }
        else if(cmd.startsWith('axisSelectJog')){
          if(stringToAxisSelectJogMap.hasOwnProperty(cmd)){
            const action = stringToAxisSelectJogMap[cmd](axisSelectJogAxis, trajCoords);
            onAction(action);
            tick.play();
          }
          else{
            if(location.startsWith(MANUAL_TAB) || location.startsWith(SETUP_TAB)) {
              if(enableKeyboardJogging) {
                const jointNum = trajCoords.indexOf(axisSelectJogAxis);
                if(jointNum !== -1 && axisSelectJogDigitIndex > 0 && axisSelectJogDigitIndex < 5){
                  const displayUnit = jointNum > 2 ? displayUnits.angle : displayUnits.length;
                  const incrementBase = jointNum > 2 ? 0.001 : displayUnit === 'mm' ? 0.001 : 0.0001;
                  const increment = incrementBase * Math.pow(10, axisSelectJogDigitIndex - 1);
                  const machineUnitIncrement = toMachineUnits({value: increment, unit: displayUnit}, unitsCode).value;
                  const direction = cmd.includes('+') ? +1 : -1; //The 5th character represents direction: + or -
                  const speed = jointNum > 2 ? 10 : 1;
                  onJogIncrement(jointNum, direction * speed, machineUnitIncrement);
                  tick.play();
                }
              }
            }
          }
        }
        else{
          const action = stringToActionMap[cmd]();
          onAction(action)
          tick.play();
        }
      }
    },
    onKeyUp: (e) => {
      let code = e.nativeEvent.code;
      if(code.startsWith('Digit')){
        code = code.slice(5);
      }
      else if(e.nativeEvent.code.startsWith('Key')){
        code = code.slice(3);
      }
      if(shortcutsMap.hasOwnProperty(code) && shortcutsMap[code].startsWith('jog')){
        const actionKey = shortcutsMap[code];
        const jointNum = trajCoords.indexOf(actionKey[3]);
        onJogStop(jointNum)
      }
    }
  };
})(KeyboardShortcuts);
