import React, { useRef, useState, useCallback } from 'react';
import { makeStyles } from '@material-ui/styles';
import { useTheme } from '@material-ui/core/styles';
// import produce from 'immer';
import Box from '@material-ui/core/Box';
import Button from '@material-ui/core/Button';
// import ButtonGroup from '@material-ui/core/ButtonGroup';
// import Divider from '@material-ui/core/Divider';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';

import Grid from '@material-ui/core/Grid';
import IconButton from '@material-ui/core/IconButton';
import InputAdornment from '@material-ui/core/InputAdornment';
// import List from '@material-ui/core/List';
// import ListItem from '@material-ui/core/ListItem';
// import Paper from '@material-ui/core/Paper';
import Tab from '@material-ui/core/Tab';
// import Table from '@material-ui/core/Table';
import Tabs from '@material-ui/core/Tabs';
import TextField from '@material-ui/core/TextField';
// import Toolbar from '@material-ui/core/Toolbar';
// import Tooltip from '@material-ui/core/Tooltip';
// import Typography from '@material-ui/core/Typography';


// import DeleteIcon from '@material-ui/icons/Delete';
// import SendIcon from '@material-ui/icons/Send';
import StarIcon from '@material-ui/icons/Star';
// import StarBorderIcon from '@material-ui/icons/StarBorder';

// import { MDI_MEMORY_PROPS } from '../modules/rockhopper/constants';

import FavoritesTable from './FavoritesTable';
import MdiHistoryList from './MdiHistoryList';
import MdiQuickAccessGrid from './MdiQuickAccessGrid';
import Panel from './Panel';
import TabPanel from './TabPanel';

// import MdiTextField from './MdiTextField';
// import SubmittableTextField from './SubmittableTextField';


// const mdiSortOrder = -1;

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  };
}


const useStyles = makeStyles((theme) => ({
  container: {
    borderRadius: 4,
  },
  inputBox: {
    flexGrow: 1,
    display: 'flex',
    backgroundColor: theme.palette.background.paper,
  },
  inputButton: {
    borderTopRightRadius: 0,
    borderBottomRightRadius: 0,
  },
  tabContainer: {
    // maxHeight: 1400,
    minHeight: 600,
    maxHeight: 800,
    overflow: 'auto',
    // float: 'right',
  },
  listItem: {
      '&:nth-of-type(odd)': {
        backgroundColor: theme.palette.action.hover,
      },
  },
  mdi: {
    width: "100%",
  },
  mdiInput: {
    borderBottomLeftRadius: 0,
    borderBottomRightRadius: 0,
  },
  noPadding: {
    padding: 0,
  },
  root: {
    flexGrow: 1,
    display: 'flex',
    backgroundColor: theme.palette.background.paper,
  },
  submitButton: {
    width: "100%",
    justifyContent: "left",
  },
  tabPanel: {
    padding: 0,
    height: 600,
  }
}));

const Mdi = (props) => {
  const {
    readyForMdi,
    // mdiSortKey,
    // mdiText,
    // onSetMdiText,
    onSendMdiCmd,
    onAddMdiFavorite,
    onRemoveMdiFavorites,
    onNameMdiFavorite,
    mdiHistory,
    mdiFavorites,
    mdiFavoritesMap,
    mdiHistoryInFavoritesMap,
    mdiHistorySorted,
    mdiHistorySortedUnique,
  } = props;
  const classes = useStyles();
  const theme = useTheme();

  const commandRef = useRef(null);

  const [mdiText, setMdiText] = useState("")
  const [isCommandFocused, setIsCommandFocused] = useState(false);
  const [tabIndex, setTabIndex] = useState(0);
  const [isRemoveFavoriteDialogOpen, setIsRemoveFavoriteDialogOpen] = useState(false);
  const [cmdOnRemoveFavoriteDialog, setCmdOnRemoveFavoriteDialog] = useState("");

  const handleAddMdiFavorite = useCallback((cmd) => {
    if(onAddMdiFavorite) {
      onAddMdiFavorite(cmd, mdiFavorites);
    }
  }, [onAddMdiFavorite, mdiFavorites]);

  const handleRemoveMdiFavorites = useCallback((cmdSet) => {
    if(onRemoveMdiFavorites) {
      console.log(cmdSet)
      //If removing a single favorite, and the favorite has a name
      //Open dialog to confirm before deleting
      if(cmdSet.size === 1){
        const cmd = cmdSet.values().next().value;
        const name = mdiFavoritesMap.get(cmd).name;
        console.log(cmd)
        if(name && name.length > 0){
          setCmdOnRemoveFavoriteDialog(cmd);
          setIsRemoveFavoriteDialogOpen(true);
          return;
        }
      }
      onRemoveMdiFavorites(cmdSet, mdiFavoritesMap);
    }
  }, [onRemoveMdiFavorites, mdiFavoritesMap]);

  const handleSubmitRemoveFavoriteDialog = useCallback(() => {
    let cmdSet = new Set();
    console.log(cmdOnRemoveFavoriteDialog);
    cmdSet.add(cmdOnRemoveFavoriteDialog);
    setCmdOnRemoveFavoriteDialog("");
    setIsRemoveFavoriteDialogOpen(false);
    if(onRemoveMdiFavorites){
      onRemoveMdiFavorites(cmdSet, mdiFavoritesMap);
    }
  }, [cmdOnRemoveFavoriteDialog, setIsRemoveFavoriteDialogOpen, onRemoveMdiFavorites, mdiFavoritesMap]);

  const handleNameMdiFavorite = useCallback((cmd, name) => {
    if(onNameMdiFavorite) {
      onNameMdiFavorite(cmd, name, mdiFavorites);
    }
  }, [onNameMdiFavorite, mdiFavorites]);

  const handleTextFieldSubmit = useCallback((e) => {
    const v = e.target.value;
    if(onSendMdiCmd) {
      onSendMdiCmd(v, mdiHistory);
    }
  }, [ onSendMdiCmd, mdiHistory ]);

  const handleSendMdiCommand = useCallback((v) => {
    if(onSendMdiCmd) {
      onSendMdiCmd(v, mdiHistory)
    }
  }, [ onSendMdiCmd, mdiHistory ]);

  const handlePopulateCommand = useCallback((cmd) => {
    // commandRef.current.value = cmd;
    setMdiText(cmd)
    
    // setCommandText(cmd);
    setIsCommandFocused(true);
    setTimeout(() => {
      commandRef.current.focus();
    }, 100);
  }, [setMdiText, setIsCommandFocused, commandRef]);

  const handleChangeTabs = useCallback((event, newValue) => {
    setTabIndex(newValue);
  },[setTabIndex]);

  const handleChange = useCallback((e) => {
    setMdiText(e.target.value);
    // if(onSetMdiText) {
    //   onSetMdiText(e.target.value);
    // }
  },[]);

  const handleKeyDown = useCallback((e) => {
    if(e.key === "Enter") {
      if(onSendMdiCmd) {
        onSendMdiCmd(e.target.value, mdiHistory);
      }
      e.target.blur();
    }
  }, [onSendMdiCmd, mdiHistory]);

  const handleCloseRemoveDialog = () => {
    setIsRemoveFavoriteDialogOpen(false);
  }


  return <Panel label="MDI">  
      <Box className={classes.inputBox}>
        {/* <MdiTextField
         inputRef={commandRef}
         onSubmit={handleTextFieldSubmit}
         onKeyDown={handleKeyDown}
         onChange={handleChange}
         variant="outlined"
         classes={{ 
           root: classes.mdi,
           // input: classes.mdi
         }}
         InputProps={{
           // className: `${classes.mdiInput} ${isCommandFocused ? "Mui-focused" : undefined }`,
           endAdornment: (
             commandRef.target && commandRef.target.value.length > 0 ? (
             <InputAdornment position="end">
               <IconButton
                 onClick={(e) => { handleAddMdiFavorite(commandRef.target.value) }}
               >
                 <StarIcon/>
               </IconButton>
             </InputAdornment>
             ) : null
           ),
         }}
         label={"Enter MDI..."}
         mdiText={mdiText}
       /> */}
        <TextField 
          disabled={!readyForMdi.isReady}
          inputRef={commandRef} 
          onSubmit={handleTextFieldSubmit}
          onKeyDown={handleKeyDown}
          onChange={(e) => handleChange(e)}
          variant="outlined"
          classes={{ 
            root: classes.mdi,
          }}
          InputProps={{
            className: `${classes.mdiInput} ${isCommandFocused ? "Mui-focused" : undefined }`,
            endAdornment: (
              commandRef.target && commandRef.target.value.length > 0 ? (
              <InputAdornment position="end">
                <IconButton
                  onClick={(e) => { handleAddMdiFavorite(commandRef.target.value) }}
                >
                  <StarIcon/>
                </IconButton>
              </InputAdornment>
              ) : null
            ),
          }}
          label={"Enter MDI..."}
          value={mdiText}
        />
      </Box>
      <Grid container>
        <Grid item xs={3} sm={3} md={2}>
          <Box border={1} borderColor={theme.palette.grey[200]}>
            <Tabs
              value={tabIndex}
              onChange={handleChangeTabs}
              aria-label="simple tabs example"
              orientation="vertical"
            >
              <Tab label="Recent" {...a11yProps(0)}/>
              <Tab label="Log" {...a11yProps(1)}/>
              <Tab label="Favorites" {...a11yProps(2)}/>
              <Tab label="Quick Access" {...a11yProps(3)}/>
            </Tabs>
          </Box>
        </Grid>
        <Grid item xs className={classes.tabContainer}>
          <TabPanel value={tabIndex} index={0} className={classes.tabPanel} classes={{root: classes.noPadding}}>
            <MdiHistoryList
              mdiHistory={mdiHistorySortedUnique}
              mdiHistoryInFavoritesMap={mdiHistoryInFavoritesMap}
              onPopulate={handlePopulateCommand}
              onAddFavorite={handleAddMdiFavorite}
              onRemoveFavorite={handleRemoveMdiFavorites}
            />
          </TabPanel>
          <TabPanel value={tabIndex} index={1} className={classes.tabPanel} classes={{root: classes.noPadding}}>
            <MdiHistoryList
              mdiHistory={mdiHistorySorted}
              mdiHistoryInFavoritesMap={mdiHistoryInFavoritesMap}
              onPopulate={handlePopulateCommand}
              onAddFavorite={handleAddMdiFavorite}
              onRemoveFavorite={handleRemoveMdiFavorites}
            />
          </TabPanel>
          <TabPanel value={tabIndex} index={2} className={classes.tabPanel} >
            <FavoritesTable
              favorites={mdiFavorites ? mdiFavorites : []}
              onNameFavorite={handleNameMdiFavorite}
              onDelete={handleRemoveMdiFavorites}
              onClickItem={handlePopulateCommand}
            />
          </TabPanel>
          <TabPanel value={tabIndex} index={3} className={classes.tabPanel} p={3} >
            <Grid container spacing={1}>
              <MdiQuickAccessGrid
                mdiFavorites={mdiFavorites}
                onClickItem={handleSendMdiCommand}
              />
            </Grid>
          </TabPanel>
        </Grid>
      </Grid>
      <Dialog open={isRemoveFavoriteDialogOpen} onClose={handleCloseRemoveDialog} aria-labelledby="form-dialog-title">
        <DialogTitle id="form-dialog-title">Removing Named Favorite</DialogTitle>
        <DialogContent>
          <DialogContentText noWrap>
            <Box component="span" >G-Code:</Box>
            <Box component="span"  fontWeight="fontWeightBold">{cmdOnRemoveFavoriteDialog}</Box>
          </DialogContentText>
          <DialogContentText noWrap>
            <Box component="span" >Name:</Box>
            <Box component="span"  fontWeight="fontWeightBold">{mdiFavorites && cmdOnRemoveFavoriteDialog ? mdiFavoritesMap.get(cmdOnRemoveFavoriteDialog).name : ""}</Box>
          </DialogContentText>
          <DialogContentText noWrap>
            <Box component="span" >Saved:</Box>
            <Box component="span"  fontWeight="fontWeightBold">{mdiFavorites && cmdOnRemoveFavoriteDialog ? new Date(mdiFavoritesMap.get(cmdOnRemoveFavoriteDialog).time).toLocaleString("en-US") : ""}</Box>
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCloseRemoveDialog} color="primary">
            Cancel
          </Button>
          <Button onClick={handleSubmitRemoveFavoriteDialog} color="primary">
            Remove
          </Button>
        </DialogActions>
      </Dialog>
  </Panel>;
};
export default Mdi;
